@import '../base.scss';

.c-featured-articles-slider {
  @include t-gutters(true);
  @include t-v-gutters;
  margin-bottom: 4.375rem;

  &__double-image {
    position: relative;
    overflow: hidden;

    &-background {
      @include t-absolute-full;

      &-container {
        padding-bottom: 103.6%;
      }
    }

    &-overlay {
      object-fit: cover;
      width: auto;
      height: 100%;

      &-container {
        width: auto;
        height: 100%;
        @include t-absolute-center-x-y;
        height: 70%;
      }
    }
  }

  &__content-container {
    text-align: center;
  }

  &__title {
    margin: 0 0 t-to-rem(20);
  }

  &__slides {
    margin-bottom: t-to-rem(40);

    &.keen-slider {
      overflow-x: hidden;
    }
  }

  &__slide {
    flex-shrink: 0;
    width: 100%;
  }

  &__article {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &-title {
      color: $s-color--font-1;
      margin: 0 t-to-rem(26) t-to-rem(32);
    }

    &-image {
      &-container {
        width: 65%;
        margin: 0 auto;
        padding-bottom: 65%;
      }
    }
  }

  &__dots {
    display: flex;
    justify-content: center;
    margin-bottom: t-to-rem(32);
  }

  &__dot {
    @include t-width-and-height(10);
    background-color: $s-color--font-5;
    margin-right: t-to-rem(22);
    border-radius: 100%;
    cursor: pointer;

    &:last-of-type {
      margin-right: 0;
    }

    &.is-active {
      background-color: $s-color--font-1;
    }
  }

  &__view-all-btn {
    @extend %s-btn--primary;
    padding: t-to-rem(15) t-to-rem(35);
  }

  @include t-media-query($s-medium-down) {
    &__double-image {
      margin-bottom: t-to-rem(40);
    }

    &__content-container {
      margin-top: t-to-rem(40);
    }
  }

  @include t-media-query($s-medium-up) {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__double-image {
      width: 50%;
    }

    &__content-container {
      width: t-to-rem(400);
      margin: 0 auto;
    }

    &__double-image {
      order: 2;
    }

    &__article {
      &-title {
        font-size: t-to-rem(32);
        margin: 0 0 t-to-rem(66);
      }
    }

    &__slides {
      margin-bottom: 6.6%;
    }

    &__dots {
      margin-bottom: 14.26%;
    }

    &__dot {
      @include t-width-and-height(11);
    }
  }

  @include t-media-query($s-xlarge-up) {
    align-items: flex-start;

    &__double-image {
      width: 49.7%;
    }

    &__double-image-background-container {
      padding-bottom: 103%;
    }

    &__content-container {
      width: 40%;
      margin: 5.4375rem 1.25rem 0;
    }

    &__title {
      margin-bottom: 1.875rem;
    }

    &__article {
      &-title {
        max-width: 22.1875rem;
        margin: 0 auto 1.875rem;
        font-size: t-to-rem(36);
      }
    }

    &__article-image-container {
      padding-bottom: 49%;
      width: 49%;
    }

    &__slides {
      margin-bottom: 8%;
    }

    &__dots {
      margin-bottom: 7.26%;
    }

    &__dot {
      @include t-width-and-height(12);
    }
  }
}